import React from 'react'
import './contact.css'
import {MdOutlineEmail} from 'react-icons/md'
import {RiMessengerLine} from 'react-icons/ri'
import { useRef } from 'react';
import emailjs from 'emailjs-com' 

const contact = () => {
  const form = useRef();
  
  const sendEmail = (e) => {
    e.preventDefault();

    emailjs.sendForm('service_9lu45iq', 'template_6r5ulg2', form.current, 'oc1cXM-GBmgIxGXrk')
     
  e.target.reset()
  };
  return (
   <section id="contact">
        <h1>Get In Touch</h1>
        <h1>Contact Me</h1>

        <div className="container contact__container">
          <div className="contact__options">
            <article className="contact__option">
              <MdOutlineEmail className='contact__option-icon'/>
              <h4>Email</h4>
              <h5>yienz25@gmail.com</h5>
              <a href="mailto:yienz25@gmail.com" target='_blank'>Send a message</a>
            </article>

            <article className="contact__option">
              <RiMessengerLine className='contact__option-icon'/>
              <h4>Messenger</h4>
              <h5>yienz25@gmail.com</h5>
              <a href="https://m.me/Yien25" target='_blank'>Send a message</a>
            </article>

      
          </div>
      
        {/* End OF CONTACT */}

        <form ref={form} onSubmit={sendEmail}>
          <input type="text" name="name" placeholder ="Your Full Name" required />
          <input type="email" name="email" placeholder ="Your Email" required />
          <textarea type="message" rows="7" placeholder ="Your Message" ></textarea>
          <button type="submit" className='btn btn-primary'>Send Message</button>

        </form>

  </div>
   </section>

  )
}

export default contact