import React from 'react'
import './portfolio.css'
import IMG1 from '../../assets/ebook.jpg'
import IMG2 from '../../assets/S1.png'
import IMG3 from '../../assets/marketplace1.jpg'
import IMG4 from '../../assets/portfolio4.jpg'
import IMG5 from '../../assets/portfolio5.png'
import IMG6 from '../../assets/portfolio6.jpg'

const data = [
  {
    id:1,
    image:IMG1,
    title: 'Ebook Library Application',
    github: 'https://github.com',
    demo: 'https://dribbble.com/shots/20669967-DNA-Research-Mobile-IOS-App?utm_source=Clipboard_Shot&utm_campaign=purrwebui&utm_content=DNA%20Research%20Mobile%20IOS%20App&utm_medium=Social_Share&utm_source=Clipboard_Shot&utm_campaign=purrwebui&utm_content=DNA%20Research%20Mobile%20IOS%20App&utm_medium=Social_Share'
  },
  {
    id:2,
    image:IMG2,
    title: 'TO-DO LIST',
    github: 'https://github.com/yhien25/todo.github.io',
    demo: 'https://luxury-piroshki-d9e0d5.netlify.app/'
  },
  {
    id:3,
    image:IMG3,
    title: 'Online Marketplace',
    github: 'https://github.com/yhien25/Omp.git',
    demo: 'https://eompdemo.000webhostapp.com'
  },

]




const portfolio = () => {
  return (
    <section id="portfolio">
      <h1>My Recent Work</h1>
      <h1>Portfolio</h1>

      <div className="container portfolio__container">
        {
          data.map(({id, image, title, github, demo}) => {
            return (

          <article key={id} className='portfolio__item'>
          <div className='portfolio__item-image'>

            <img src={image} alt={title}/>

            </div> 
        <h3>{title}</h3>
        <div className='portfolio__item-cta'>
        <a href={github} className='btn'>Github</a>
        <a href={demo} className='btn btn-primary' target='_blank'>Live Demo</a>
</div>
        </article>

              
            )
            })
        }
        
      


      </div>
    </section>
  )
}

export default portfolio